const initState = {
  videos: [],
  selectedVideo: null,
};

const searchReducer = (state = initState, action) => {
  switch (action.type) {
    case "FETCH_SEARCHED":
      return {
        ...state,
        videos: action.payload.videos,
        selectedVideo: action.payload.selectedVideo,
      };
    case "FETCH_SELECTED":
      return {
        ...state,
        selectedVideo: action.payload.selected,
      };
    default:
      return { ...state };
  }
};

export default searchReducer;
