import youtube from "../api/youtube";

export const loadVideo = (term) => async (dispatch) => {
  const videoData = await youtube.get("/search", {
    params: {
      q: term,
    },
  });
  dispatch({
    type: "FETCH_SEARCHED",
    payload: {
      videos: videoData.data.items,
      selectedVideo: videoData.data.items[0],
    },
  });
};
